<template>
  <v-container>
		<div class="login-wrap">
			<div class="login-html">
				<input id="tab-1" type="radio" name="tab" class="sign-in" checked><label for="tab-1" class="tab">로그인</label>
				<input id="tab-2" type="radio" name="tab" class="sign-up"><label for="tab-2" class="tab">회원가입</label>
				<div class="login-form">
					<div class="sign-in-htm">
						<div class="group">
							<label for="loginuser" class="label">아이디</label>
							<input v-model="loginuser" type="text" class="input">
						</div>
						<div class="group">
							<label for="loginpass" class="label">비밀번호</label>
							<input v-model="loginpass" type="password" class="input" data-type="password">
						</div>
						<div class="group">
							<input id="check" type="checkbox" class="check" checked>
							<label for="check"><span class="icon"></span>로그인 유지하기</label>
						</div>
						<div class="group">
							<input type="submit" class="button" value="로그인" @click="login">
							<br>
							<input type="submit" class="button" value="홈으로" @click="home">
						</div>
						<div class="hr"></div>
						<div class="foot-lnk">
							<a href="#forgot">비밀번호를 잊으셨나요?</a>
						</div>
					</div>
					<div class="sign-up-htm">
						<div class="group">
							<label class="label">아이디</label>
							<input v-model="user" type="text" class="input" minlength="6" maxlength="12" placeholder="6~12자">
						</div>
						<div class="group">
							<label for="pass" class="label">비밀번호</label>
							<input v-model="pass" type="password" class="input" data-type="password" minlength="6" maxlength="12" placeholder="6~12자">
						</div>
						<div class="group">
							<label for="passcheck" class="label">비밀번호 확인</label>
							<input v-model="passcheck" type="password" class="input" data-type="password" placeholder="비밀번호 재입력">
						</div>
						<div class="group">
							<label for="email" class="label">이메일 주소</label>
							<input v-model="email" type="text" class="input" placeholder="ex)abc@qwer.com" maxlength="25">
						</div>
						<div class="group">
							<label for="name" class="label">이름</label>
							<input v-model="name" type="text" class="input" placeholder="ex)홍길동" maxlength="12">
						</div>
						<div class="group">
							<label for="nickname" class="label">닉네임</label>
							<input v-model="nickname" type="text" class="input" placeholder="ex)뽀로로" maxlength="10">
						</div>
						<div class="group">
							<label for="birth" class="label">생년월일</label>
							<input v-model="birth" type="text" class="input" placeholder="ex)2000-03-03" maxlength="10">
						</div>
						<div class="group">
							<label for="user_type" class="label">타입 - 교육자, 학생</label>
							<select v-model="user_type">
								<option value="student" selected>학생</option>
								<option value="teacher">교육자</option>
							</select>
						</div>
						<div class="group">
							<input type="submit" class="button" value="회원가입" @click="join">
						</div>
						<div class="hr"></div>
						<div class="foot-lnk">
							<label for="tab-1">이미 회원이신가요?</label>
						</div>
					</div>
				</div>
			</div>
		</div>
  </v-container>
</template>

<script>
import axios from 'axios'
export default {
	name: 'loginout',
	data(){
		return{
			user : "",
			pass : "",
			passcheck : "",
			email : "",
			name : "",
			nickname : "",
			birth : "",
			user_type : "student",

			loginuser : "",
			loginpass : ""
		}
	},
	methods : {
		join() {
			let emailpattern = /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i;
			let birthpattern = /^(19[0-9][0-9]|20\d{2})-(0[0-9]|1[0-2])-(0[1-9]|[1-2][0-9]|3[0-1])$/;
			let User = {
				'userId' : this.user,
				'userPw' : this.pass,
				'userName' : this.name,
				'nickName' : this.nickname,
				'userType' : this.user_type,
				'birthDate' : this.birth,
				'email' : this.email
			}
			var config={
                header:{
                    'Content-Type' : 'application/json',
                }
			};
			if(this.user.length < 6  || this.user.length > 12){
				alert("아이디 길이를 확인하세요.")
				return;
			}
			if(this.pass.length < 6  || this.pass.length > 12){
				alert("패스워드 길이를 확인하세요.")
				return;
			}
			if(emailpattern.test(this.email)==false){
				alert("이메일 형식을 확인하세요.")
				return;
			}
			if(birthpattern.test(this.birth)==false){
				alert("생일 형식을 확인하세요.")
				return;
			}

			axios.post('/user/sign',User,config)
			.then((res)=>{
				res
				alert("회원가입완료")
				this.$router.push('/')
			})
			
		},
		login(){
			let User = {
				'userId':this.loginuser,
				'userPw':this.loginpass
			}

			var config={
                header:{
                    'Content-Type' : 'application/json',
                }
			}
			axios.post('/user/login',User,config)
			.then((response)=>{
				if (response.status === 200 && response.headers.authorization) {

                        this.$session.start();
                        this.$session.set('jwt', response.headers.authorization);
                        axios.defaults.headers.common['Authorization'] = response.headers.authorization;
                        this.$store.dispatch('LOGIN',response.data);
                        
                        this.$router.push('/');
                }else{
                        alert('아이디와 비밀번호를 확인해주세요');
                }
			}).catch((err)=>{
				err
				alert('아이디와 비밀번호를 확인해주세요System Error');
			})
		},
		home() {
			this.$router.push('/')
		}
	}

}
</script>

<style scoped>
body{
	background-color: #e9e9e9;
	font-family: 'Montserrat', sans-serif;
	font-size: 16px;
	line-height: 1.25;
	letter-spacing: 1px;
}
*,:after,:before{box-sizing:border-box}
.clearfix:after,.clearfix:before{content:'';display:table}
.clearfix:after{clear:both;display:block}
a{color:inherit;text-decoration:none}

.login-wrap{
	width:100%;
	margin:auto;
	max-width:525px;
	min-height:670px;
	position:relative;
	background-image: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/283591/login-background.jpg);
	box-shadow: 0 50px 70px -20px rgba(0, 0, 0, 0.85);
}
.login-html{
	width:100%;
	height:100%;
	position:absolute;
	padding:90px 70px 50px 70px;
	background:rgba(40,57,101,.9);
}
.login-html .sign-in-htm,
.login-html .sign-up-htm{
	top:0;
	left:0;
	right:0;
	bottom:0;
	position:absolute;
	transform:rotateY(180deg);
	backface-visibility:hidden;
	transition:all .4s linear;
}
.login-html .sign-in,
.login-html .sign-up,
.login-form .group .check{
	display:none;
}
.login-html .tab,
.login-form .group .label,
.login-form .group .button{
	text-transform:uppercase;
}
.login-html .tab{
	font-size:22px;
	margin-right:15px;
	padding-bottom:5px;
	margin:0 15px 10px 0;
	display:inline-block;
	border-bottom:2px solid transparent;
}
.login-html .sign-in:checked + .tab,
.login-html .sign-up:checked + .tab{
	color:#fff;
	border-color:#1161ee;
}
.login-form{
	min-height:345px;
	position:relative;
	perspective:1000px;
	transform-style:preserve-3d;
}
.login-form .group{
	margin-bottom:15px;
}
.login-form .group .label,
.login-form .group .input,
.login-form .group .button{
	width:100%;
	color:#fff;
	display:block;
}
.login-form .group .input,
.login-form .group .button{
	border:none;
	padding:15px 20px;
	border-radius:25px;
	background:rgba(255,255,255,.1);
}
.login-form .group input[data-type="password"]{
	text-security:circle;
	-webkit-text-security:circle;
}
.login-form .group .label{
	color:#aaa;
	font-size:12px;
}
.login-form .group .button{
	background:#1161ee;
}
.login-form .group label .icon{
	width:15px;
	height:15px;
	border-radius:2px;
	position:relative;
	display:inline-block;
	background:rgba(255,255,255,.1);
}
.login-form .group label .icon:before,
.login-form .group label .icon:after{
	content:'';
	width:10px;
	height:2px;
	background:#fff;
	position:absolute;
	transition:all .2s ease-in-out 0s;
}
.login-form .group label .icon:before{
	left:3px;
	width:5px;
	bottom:6px;
	transform:scale(0) rotate(0);
}
.login-form .group label .icon:after{
	top:6px;
	right:0;
	transform:scale(0) rotate(0);
}
.login-form .group .check:checked + label{
	color:#fff;
}
.login-form .group .check:checked + label .icon{
	background:#1161ee;
}
.login-form .group .check:checked + label .icon:before{
	transform:scale(1) rotate(45deg);
}
.login-form .group .check:checked + label .icon:after{
	transform:scale(1) rotate(-45deg);
}
.login-html .sign-in:checked + .tab + .sign-up + .tab + .login-form .sign-in-htm{
	transform:rotate(0);
}
.login-html .sign-up:checked + .tab + .login-form .sign-up-htm{
	transform:rotate(0);
}

.hr{
	height:2px;
	margin:60px 0 50px 0;
	background:rgba(255,255,255,.2);
}
.foot-lnk{
	text-align:center;
}
</style>